import { BlueSectionWrapper, PageWrapper, PinkSectionWrapper, Text, Title } from "../pages.styles"
import { MessageForm } from "./components"
import { ContentWrapper, ListWrapper, Header, Image, HeaderWrapper } from "./contact.styles";
import { Information } from "../home/components/introduction/components";
import image from "../../assets/androidphones-2048px-N20front.webp";

export const Contact = () => {
    return(
        <PageWrapper>
            <PinkSectionWrapper>
                <ContentWrapper>
                    <HeaderWrapper>
                        <Text>
                            CONTACT ME
                        </Text>
                        <Header>
                            Got a question? Reach out and I will answer ASAP!
                        </Header>
                    </HeaderWrapper>
                    <Image src={image} alt="placeholder"/>
                </ContentWrapper>
            </PinkSectionWrapper>
            <BlueSectionWrapper>
                <ContentWrapper>
                    <MessageForm/>
                    <ListWrapper>
                        <Information/>
                    </ListWrapper>
                </ContentWrapper>
            </BlueSectionWrapper>
        </PageWrapper>
    )
}