import { Dispatch, SetStateAction } from "react";
import { MenuItem } from "./components";
import { NavigationWrapper } from "./navigation.styles";
import { pages } from "../../../../types";

type HeaderProps = {
    setPage: Dispatch<SetStateAction<pages>>;
}

export const Navigation = ({setPage}: HeaderProps) => {
    const headerTitles = [pages.PROJECTS, pages.CONTACT];

    return(
        <NavigationWrapper>
            {
                headerTitles.map((title) => <MenuItem title={title} key={title} onClick={() => setPage(title)}/>)
            }
        </NavigationWrapper>
    )
}