import styled from 'styled-components';

export const PageWrapper = styled.div`
    max-width: 100%;
    position: relative;
`;

export const SectionWrapper = styled.div`
    padding: 36px 560px;
    min-height: 360px;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

export const BlueSectionWrapper = styled(SectionWrapper)`
    background-color: #A0D0CA;
`;

export const PinkSectionWrapper = styled(SectionWrapper)`
    background-color: #E9A2A1;
`

export const WhiteSectionWrapper = styled(SectionWrapper)`
    background-color: #FFFFFF;
`

export const HeroSectionWrapper = styled(SectionWrapper)`
    min-height: 480px;
`

export const HeaderWrapper = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 36px;
    align-items: center;
`
export const Title = styled.div`
    font-size: 40px;
    text-align: center;
    font-weight: 600;
`

export const Text = styled.p`
    margin: 0px 0px;
    font-size: 18px;
`