import { Link } from "react-router-dom"
import { IconWrapper, MainIconWrapper, Subtitle, TextWrapper, Title } from "./mainIcon.styles"
import { Dispatch, SetStateAction } from "react";
import { pages } from "../../../../types";

type MainIconProps = {
    setPage: Dispatch<SetStateAction<pages>>;
}

export const MainIcon = ({setPage}: MainIconProps) => {
    return(
        <MainIconWrapper>
            <IconWrapper onClick={() => setPage(pages.START)}>
                <Link to="start">
                    Placeholder
                </Link>
            </IconWrapper>
            <TextWrapper>
                <Title>Firstname Lastname's</Title>
                <Subtitle>Personal Website</Subtitle>
            </TextWrapper>
        </MainIconWrapper>
    )
}