import { ItemWrapper, TextWrapper } from "./menuItems.styles";
import { pages } from "../../../../../../types";

type MenuItemProps = {
    title: pages;
    onClick: () => void;
}

export const MenuItem = ({
    title,
    onClick
}: MenuItemProps) => {
    return(
        <ItemWrapper onClick={() => onClick()}>
            <TextWrapper>
                {title}
            </TextWrapper>
        </ItemWrapper>
    )
}